import React, {FC, useEffect, useState} from 'react';
import Header from "./Header";
import {connect} from "react-redux";
import {authActions} from "../../redux/reducers/auth-reducer";
import {compose} from "redux";
import {withRouter, RouteComponentProps, useHistory} from "react-router-dom";
import {
    getCurrentUserInfo,
    getCurrentUserId,
    getCurrentUserLogin,
    getIsAuth
} from "../../redux/reducers/auth-selectors";
import {AppStateType} from "../../redux/redux-store";
import {getNewMessagesCount} from "../../redux/reducers/dialogs-selectors";
import {ChatParticipantsType, MessagesType, ProfileType} from "../../redux/reducers/types";
import ConfirmDialog from "../Profile/Dialogs/ConfirmDialog";
import {withSnackbar, WithSnackbarProps} from 'notistack';
import {dialogActions} from "../../redux/reducers/dialogs-reducer";
import {socket} from "../../App";

type MapStatePropsType = {
    userId: number | null,
    login: string | null,
    isAuth: boolean,
    newMessagesCount: number | null,
    currentUserInfo: ProfileType | null
}

type MapDispatchPropsType = {
    logout: (history: any) => void,
    requestNewMessagesCount: () => void
}

type PropsType = MapStatePropsType & MapDispatchPropsType & RouteComponentProps & WithSnackbarProps

const HeaderContainer: FC<PropsType> = ({enqueueSnackbar, requestNewMessagesCount, logout, userId, ...props}) => {
    const [logoutDialogIsOpen, setLogoutDialogIsOpen] = useState(false);
    let history = useHistory();

    useEffect(() => {
        socket.on('new_message', (msg: MessagesType) => {
            if(msg.recipientId === userId) {
                requestNewMessagesCount();
                enqueueSnackbar(`New message from ${msg.senderName} for you!`, {variant: 'default'});
            }
        });
        socket.on('watch_messages', (participants: ChatParticipantsType) => {
            if(participants.watcher === userId) {
                requestNewMessagesCount();
            }
        });
    }, [userId]);

    const openLogoutDialog = (open: boolean) => {
        setLogoutDialogIsOpen(open);
    };

     const logoutAction = () => {
        logout(history);
    };

    return (
        <>
            <Header {...props} userId={userId} openLogoutDialog={openLogoutDialog}/>
            <ConfirmDialog
                isOpen={logoutDialogIsOpen}
                openDialog={openLogoutDialog}
                confirmAction={logoutAction}>
                {true}
            </ConfirmDialog>
        </>
    )
};

const mapStateToProps = (state: AppStateType) => {
    return {
        userId: getCurrentUserId(state),
        login: getCurrentUserLogin(state),
        isAuth: getIsAuth(state),
        newMessagesCount: getNewMessagesCount(state),
        currentUserInfo: getCurrentUserInfo(state)
    }
};

const {logout} = authActions;
const {requestNewMessagesCount} = dialogActions;

export default compose(
    connect<MapStatePropsType, MapDispatchPropsType, RouteComponentProps, AppStateType>(mapStateToProps, {logout, requestNewMessagesCount}),
    withSnackbar,
    withRouter
)(HeaderContainer) as FC;
